import React, { Component} from "react";
import api from "./Api";
import meta from "./meta";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Table } from "react-bootstrap";


function fmtMSS(s){return (s-(s%=60))/60+(9<s?':':':0')+parseInt(s);}

class App extends Component {
state = {
  clientes: [],
  tmas: [],
  tmes: [],
  nivel: [],
  feitas: [],
  recebidas: [],
  atendiadas: [],
  abandonadas: [],
  apmeta: [],
}


async componentDidMount(){


    this.interval = setInterval(async () => {
    const response = await api.get("../monitor/calls_in_queue");
    this.setState({ clientes: response.data.result });
  }, 1000);

  this.interval = setInterval(async () => {
    const response = await api.get("../dashboard/supervisor");
    this.setState({ feitas: response.data.result.stats.stats_all.stats.counts.outgoing_answer.start_of_this.day});
    this.setState({ abandonadas: response.data.result.stats.stats_all.stats.counts.incoming_lost.start_of_this.day});
    this.setState({ recebidas: response.data.result.stats.stats_all.stats.counts.incoming.start_of_this.day});
    this.setState({ atendiadas: response.data.result.stats.stats_all.stats.counts.incoming_answer.start_of_this.day});
  }, 3000);

  const teste = await api.get("../monitor/stats_summary");
  this.setState({ tmes: teste.data.result.stats.avgs.wait_time.start_of_this.day.avg });
  this.setState({ tmas: teste.data.result.stats.avgs.talk_time.start_of_this.day.avg });
  this.setState({ nivel: teste.data.result.stats.service_levels.start_of_this.day });

  const apmeta = await meta.get("../api/user/current");
  this.setState({ apmeta: apmeta.data });

}



render(){

  const { clientes } = this.state;
  let { tmas } =this.state;
  let { tmes } =this.state;
  const { nivel } = this.state;
  const emFila = clientes.length;
  const { feitas } = this.state;
  const { recebidas } = this.state;
  const { atendiadas } = this.state;
  const { abandonadas } = this.state;
  const { apmeta } = this.state;
  console.log(apmeta);


  return (
    <Container fluid style={{fontFamily:'arial black'}} xl>
      <Row>
        <Col md={12}>
          <iframe 
              src="https://emex.hubsoft.com.br:8443/public/question/f5bdc538-e439-4038-bc64-7041deef8b9a"
              frameBorder="0"
              width="1850"
              height="250"
              allowtransparency="true"
              title='notificação'
              className = 'succecs'>
          </iframe>
        </Col>
      </Row>

      <Row style={{backgroundColor:'#60DD60'}}>
        <Col style={{fontSize:40,justifyContent:'center'}}>
          <text class="row justify-content-center align-items-center">ATENDIMENTO URA</text>
        </Col>
      </Row>

      <Row style={{backgroundColor:'#60DD60'}}>
        <Col md={2}>
        <h1>Fila Atual</h1>
        <ul style={{listStyle: 'none'}}>
        {clientes.map(cliente =>(
          <p style={{backgroundColor:'#074A08',color:'black',borderRadius:8,padding:5,margin:5}}>
          <li key={cliente.linkedid}> 
            <text style={{fontSize:25,backgroundColor: 'white',borderRadius: '80%'}}>{cliente.position}ª </text>
            <text style={{fontSize:20,color:'white'}}>{cliente.src}</text>

            <h4 style={{color:'white'}}>{cliente.queue}</h4>
            <h4 style={{color:'white'}}>{fmtMSS(cliente.wait)}</h4>
          </li>
          </p>))}
         </ul> 
        </Col>
        <Col md={8}>
          <Table class="table table-borderless text-center" style={{marginBottom:'100%'}}>
            <thead  class="text-center">
              <tr>
                <th colSpan='2'>
                  <p style={{backgroundColor:'#074A08',color:'white',borderRadius:8,padding:5,margin:5}}>
                  <text style={{fontSize:45}}>{fmtMSS(tmes)}</text>
                  <br></br>
                  <text style={{fontSize:25}}>TME</text>
                  </p>
                  </th>
                <th  colSpan='2'>
                  <p style={{backgroundColor:'#074A08',color:'white',borderRadius:8,padding:5,margin:5}}>
                  <text style={{fontSize:45}}>{fmtMSS(tmas)}</text>
                  <br></br>
                  <text style={{fontSize:25}}>TMA</text>
                  </p>
                </th>
                <th  colSpan='2'>
                  <p style={{backgroundColor:'#074A08',color:'white',borderRadius:8,padding:5,margin:5}}>
                  <text style={{fontSize:45}}>{fmtMSS(tmas+tmes)}</text>
                  <br></br>
                  <text style={{fontSize:25}}>TMO</text>
                  </p>
                </th>  
                </tr>
                </thead>
                <tbody>
                  <tr class="text-center align-middle">
                    <td colSpan='5'>
                      <p style={{backgroundColor:'#074A08',color:'white',borderRadius:8,padding:5,margin:5}}>
                      <text style={{fontSize:25}} class="align-middle">Nivel de Serviço </text> 
                      <br></br>
                      <text style={{fontSize:60}}>{nivel}</text>
                      </p>
                    </td>
                    </tr>
                    <tr class="text-end">
                    <td>
                      <p style={{backgroundColor:'#074A08',color:'white',borderRadius:8,padding:5,margin:5}}>
                      <h1>{emFila}</h1>
                      <h2>Em fila</h2>
                      </p>
                    </td>
                    <td>
                      <p style={{backgroundColor:'#074A08',color:'white',borderRadius:8,padding:5,margin:5}}>
                      <h1>{feitas}</h1>
                      <h2>Realizadas</h2>
                      </p>
                    </td>
                    <td >
                      <p style={{backgroundColor:'#074A08',color:'white',borderRadius:8,padding:5,margin:5}}>
                      <h1>{recebidas}</h1>
                      <h2>Recebidas</h2>
                      </p>
                    </td>
                    <td>
                      <p style={{backgroundColor:'#074A08',color:'white',borderRadius:8,padding:5,margin:5}}>
                      <h1>{atendiadas}</h1>
                      <h2>Atendidas</h2>
                      </p>
                    </td>
                    <td>
                      <p style={{backgroundColor:'#074A08',color:'white',borderRadius:8,padding:5,margin:5}}>
                      <h1>{abandonadas}</h1>
                      <h2>Abandonadas</h2>
                      </p>
                    </td>
                    </tr>
                </tbody>
            </Table>
        </Col>
      </Row>
    </Container>

  );
        }
      }


export default App;